.featured{border-left:6px solid #5ba4a4}@-webkit-keyframes pulse{from{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.005, 1.005, 1.005)}to{transform:scale3d(1, 1, 1)}}@keyframes pulse{from{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.005, 1.005, 1.005)}to{transform:scale3d(1, 1, 1)}}.job{display:flex;padding:20px 32px;background-color:#fff;margin:16px 0;align-items:center;box-shadow:0 0.25rem 0.125rem 0 rgba(0,0,0,0.05);border-radius:8px;transition:all 200ms ease-in-out;transform:scale(1);transform-origin:50% 50%}@media (max-width: 425px){.job{flex-direction:column;margin:16px 0 32px}}.job:hover{cursor:pointer;transform:scale(1.005) perspective(1px);transform-origin:51% 51%;box-shadow:0 0.8rem 0.8rem 0 rgba(0,0,0,0.05)}.job .icon{margin:0 24px 0 0}@media (max-width: 425px){.job .icon{margin:0;position:absolute;top:-32px;width:64px;height:64px;left:24px}}@media (max-width: 425px){.job .icon img{width:100%}}.job .info{flex:2 1;display:flex;flex-direction:column}@media (max-width: 425px){.job .info{margin-top:24px;width:100%;justify-content:start;margin-bottom:12px}}.job .info .title-section{display:flex;align-items:center;margin-bottom:16px}.job .info .title-section .title{color:#5ba4a4;font-size:0.8rem;font-weight:bold}.job .info .title-section .badges{margin-left:12px}.job .info .title-section .badges span{margin:0 4px;padding:6px;font-size:0.5rem;text-transform:uppercase;background-color:#5ba4a4;border-radius:16px;color:#fff}.job .info .title-section .badges span:last-child{background-color:#2c3a3a}.job .info h1{font-size:1.2rem;color:#2c3a3a}.job .info .sub-info{color:#7b8e8e;margin-top:8px}.job .info .sub-info span{margin:0 12px;font-size:0.8rem;position:relative}.job .info .sub-info span:first-child{margin-left:0}.job .info .sub-info span:last-child::after{content:''}.job .info .sub-info span::after{content:' â€¢ ';position:absolute;right:-18px;color:#7b8e8e;font-size:1.5rem;top:8px;line-height:0}.job .tags{justify-content:right;flex:1 1;display:flex}@media (max-width: 425px){.job .tags{border-top:1px solid #ececec;padding-top:12px;width:100%}}.job .tags ul{padding:0;display:flex}@media (max-width: 425px){.job .tags ul{width:100%;display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:12px}}.job .tags ul li{list-style:none;margin:0 6px;padding:12px;background-color:#eef6f6;color:#5ba4a4;font-weight:bold;font-size:70%;border-radius:6px;transition:all 200ms ease-in-out}.job .tags ul li:hover{background-color:#5ba4a4;color:#fff}@media (max-width: 425px){.job .tags ul li{margin:0;text-align:center}}

.filter{display:flex;position:absolute;top:calc(-55px - 28px);background-color:#fff;width:100%;padding:0px 32px;border-radius:12px;height:70px;justify-content:space-between;align-items:center;box-shadow:0 0.8rem 0.8rem 0 rgba(0,0,0,0.05)}@media (max-width: 425px){.filter{width:100%;display:flex;min-height:70px;padding:24px;height:auto;position:relative;top:-50px}}.filter .clean{color:#7b8e8e;transition:all 200ms ease-in-out}@media (max-width: 425px){.filter .clean{display:flex;flex:1 1;justify-content:flex-end}}.filter .clean:hover{color:#5ba4a4;cursor:pointer}.filter .selected-itens{display:flex}@media (max-width: 425px){.filter .selected-itens{flex:3 1;flex-wrap:wrap;display:flex;width:100%}}.filter .selected-itens div{display:flex;align-items:center;font-size:0.8rem;color:#5ba4a4;background-color:#eef6f6;border-radius:6px;overflow:hidden;margin:0 8px}.filter .selected-itens div:first-child{margin-left:0}@media (max-width: 425px){.filter .selected-itens div{width:auto;margin:6px}.filter .selected-itens div:nth-child(2n+1){margin-left:0px}}.filter .selected-itens div span{padding:8px 12px;font-weight:bold}.filter .selected-itens div svg{color:#eef6f6;background-color:#5ba4a4;height:100%;font-size:1.8rem;padding:0 4px;transition:all 200ms ease-in-out}@media (max-width: 425px){.filter .selected-itens div svg{padding:0}}.filter .selected-itens div:hover{cursor:pointer}.filter .selected-itens div:hover svg{background-color:#2c3a3a}

